.onboarding-wrapper {
  transition: transform 0.45s ease-in, top 0.1s ease-in-out, left 0.1s ease-in-out;
  width: 1140px;
  height: 1140px;
  position: absolute;
  top: 0px;
  left: 0px;
  margin-left: -570px;
  margin-top: -570px;
  z-index: 999;

  svg {
    overflow: visible;
  }
}


/*------------------------------------*\
    $MODAL ONBOARDING
\*------------------------------------*/

.a-onboarding {
	background: $blue-darker-opacity !important;
	color: #fff;

	@include media-breakpoint-up(md) {
    .modal-dialog {
      max-width: 800px;
    }
  }

  .a-modal-bottomBG {
    margin: 310px auto;
    // background: #194160;
  }

	.modal-dialog {
    margin: 0 auto;
	}

	.modal-content {
		background: transparent;
		box-shadow: none;

    .modal-header {
      padding: 0;
      padding-bottom: $spacer*2;
      border-bottom: none;

      .modal-title {
        font-size: 36px;
      }
  	}

    .modal-body {
      padding: 0;

      .a-leadText {
        font-size: 28px;
        line-height: 38px;
        font-weight: $font-weight-light;
        margin-bottom: 0;
      }

      .patternLine {
        margin-top: $spacer*2;
      }
    }

    .modal-footer {
      margin-top: $spacer*3;
      border-top: none;
  		text-align: left;
      padding: 0;
  	}
	}
}
