
/* List in textarticle */


.a-articleRightOff {
  @include media-breakpoint-up(md) {
		max-width: 65%;
		float: right;
		margin-left: 24px;
	}

  @include media-breakpoint-up(lg) {
    max-width: 75%;
		margin-right: -192px;
	}
}

.a-text {

	h1,h2,h3,h4 {
		margin: $spacer 0 $spacer 0;

		@include media-breakpoint-up(md) {
			margin: $spacer*2 0 $spacer 0;
		}
    @include media-breakpoint-up(lg) {
			margin: $spacer*3 0 $spacer 0;
		}
	}

  h3,h4 {
    margin: $spacer*2 0 $spacer/2 0;
  }

  h2 + h3 {
    margin-top: 12px !important;
  }

	ul {
	    list-style: disc;
	}
	ul,
	ol {
	    margin: 0 0 $spacer*3 $spacer*2;
	}
  ul ul,
  ol ol {
    margin-bottom: 0;
    margin-top: 10px;
	}
	li {
		margin-bottom: 10px;
	}
	ul.no-decoration,
	ol.no-decoration {
		list-style: none;
		margin:0;
		li {
			margin: 0;
		}
	}

  // Styled lists that can appear inside article

	// Circle list inside article
	ol.a-circleList {
		li {
			margin-bottom: $spacer*2;
		}
	}

	// Unordered list with icons inside article
	ul.a-icon-list {
		li {
			margin-bottom: $spacer*1.5;
		}
	}

	// Unordered list with connections inside article
	ul.connectedBullets {
		li {
			margin: 0;
		}
	}

  // Bullet list inside article
  ul.a-bulletList {
    list-style: none;
    margin-top: $spacer;
  	margin-left: 0;

    @include media-breakpoint-up(md) {
      margin-left: -$spacer*2;
    }
  }

}



// Box

.a-box {
	background: $white;
	border: 12px solid $blue-light;
	padding: $spacer*2 $spacer*2;
	float: left;

  width: calc(100% + 48px);
  margin-left: -24px;

  @include media-breakpoint-up(sm) {
    padding: $spacer*4 $spacer*4;
    width: 100%;
    margin-left: auto;
  }

  @include media-breakpoint-up(md) {
    padding: $spacer*4 $spacer*7;
  }

	.a-box-heading {
		float: left;
		padding-bottom: $spacer*2;
	}
	.a-box-body {
		float: left;
	}
	.a-box-footer {
		padding-top: $spacer;
		float: left;
	}

	&.a-articleListDates {
		@include media-breakpoint-up(md) {
			.a-box-heading,
			.a-box-footer  {
				margin-left: $spacer*8; // When a-box is used for date list, push heading and footer
			}
		}
	}
}


// Article teaser

.a-articleTeaser {
	float: left;
	width: 100%;
	margin-bottom: $spacer;

	a {
		display: block;
	}
	a:hover,
	a:focus {
		.a-date {
			background: $blue-dark;
		}
	}
}

.a-articleTeaser-text {
	width: calc(100% - 60px);

	@include media-breakpoint-up(sm) {
		float: left;
		padding-left: $spacer*3;
	}
	@include media-breakpoint-up(md) {
		margin-top: -2px;
	}
	@include media-breakpoint-up(lg) {
		margin-top: -5px;
	}
	@include media-breakpoint-up(xl) {
		margin-top: -8px;
	}
	h2 {
		font-size: 20px;
		font-family: $font-bold; font-weight: $font-weight-bold;
		margin-bottom: 0;
	}
}

// Byline

.a-byline {
	margin-top: -$spacer/2;
	margin-bottom: $spacer*2;
}

// Date
.a-date {
	background: $blue;
	color: $white;
	text-align: center;
	width: $spacer*5;
	height: $spacer*5;
	margin-bottom: $spacer;

	@include media-breakpoint-up(sm) {
		float: left;
		margin-bottom: $spacer*3;
	}

	.a-date-day {
		font-size: $font-size-large-2;
		padding-top: 5px;
	}
	.a-date-month {
		font-size: $font-size-small;
		margin-top: -5px;
	}
}
